import React from "react";
import { Select } from "antd";
import { useQuery } from "react-query3";

export default function ModifierKeySelect(props) {
    const { data, isLoading } = useQuery<any[]>("/hotkey/modifier");
    return (
        <Select {...props} loading={isLoading} showSearch >
            {data?.map((env) => (
                <Select.Option key={env.id} value={env.name}>{env.name}</Select.Option>
            ))}
        </Select>
    );
}
